body{
    background-color: lightblue;
}
nav{
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    height: 77px;
    background-color: black;
    color: white;
}
nav .logo{
    font-size: 22px;
}
ul{
    display: flex;
    gap: 42px;
}
ul li{
    list-style: none;
    color: white;
    font-size: 22px;
    
}
.login{
    width: 400px;
    padding: 12px;
    background-color: rgb(1, 126, 119);
    text-align: center;
    margin: auto;
    margin-top: 55px;
}
.login input{
    width: 90%;
    margin-top: 21px;
    font-size: 20px;
    padding: 11px;
    border: none;
}
.login button{
    margin-top: 33px;
    cursor: pointer;
    width: 140px;
    height: 40px;
    border: none;
    background-color: black;
    color: white;
    font-size: 20px;
}
.contact{
    width: 500px;
    padding: 33px;
    background-color: black;
    color: white;
    font-size: 33px; 
    text-align: center;
    border-radius: 11px;
    margin: auto;
    margin-top: 44px;
}
.about{
